.about_container
{
    display:grid;
    grid-template-columns: 35% 50% ;
    gap:15%;
}
.about_me
{
 width: 100%;
 
 height: 95%;

 border-radius:2rem;
 background: transparent;
 display: grid;
 place-items: center;

 background-color:var(--color-primary);
 border-radius: 1rem ;
 aspect-ratio:1/1;
}
.about_me-image
{
    background:linear-gradient(var(--color-primary),transparent);
   width: 100%;
    height: 100%;
   
    /* margin-top: 4rem; */
    border-radius: 1rem;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
  background-color:teal;;;;
    
   
    overflow: hidden;
   

    transform:rotate(10deg);
    transition: var(--transition);
   

}
.about_me-image:hover
{
    transform: rotate(0);
    
}
.about_cards
{
    display: grid;
    grid-template-columns:repeat(3,1fr);
    gap:1.5rem;
}
.about_card
{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about_card:hover
{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.about_icon
{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about_card h5
{
    font-size: 0.95rem;
}
.about_card  small
{
    font-size: 0.7rem;
    color: var(--color-light);
}
.about_content p{
    margin:2rem 0 2.6rem;
    color: var(--color-light);
}


@media screen  and (max-width: 1024px)
{
.about_container
 {
    grid-template-columns: 1fr;
    gap:0 ;
    
}
.about_me
{
    width: 50%;
    height:95%;
    margin: 2rem auto 4rem;
    background-color:var(--color-primary);
    border-radius: 1rem ;
    
}
.about_me-image
{
    background:linear-gradient(var(--color-primary),transparent);
    width: 100%;
    height: 100%;
    /* margin-top: 4rem; */
    border-radius: 1rem ;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
    background-color:teal;;
    transform:rotate(10deg);
}
.about_cards
{
    margin-top:4rem;
}
.about_content p
{
    margin:1rem 0 1.5rem;
}

}

@media screen  and (max-width: 600px)
{
.about_me
{
    width: 65%;
    margin:0 auto 3rem;
    gap: 1rem;

}
.about_cards
{
    grid-template-columns:repeat(2,1fr);
    gap:1rem;
}
.about_content 
{
    text-align: center;
}
.about_content p
{
    margin:1.5rem 0;
}
}